import * as React from 'react';

import { PAGE_UPDATED_EVENT, eventPageUpdated } from '../../index';

import { GetCurrentGameProperties, GetBackgroundImage } from "../multi-scripts-use/game-properties";

import Contacts from "../multi-scripts-use/contacts/contacts";
import '../multi-scripts-use/contacts/contacts.css';

import GameTop from "./game-top";
import GameMain from "./game-main";
import GameImages from "./game-images";
import GameCode from "./game-code";

import './css/game.css';
import './css/game-top.css';
import './css/game-main.css';
import './css/game-images.css';
import './css/game-code.css';


const CURSOR_URL:string = "url(../games/games-cursor.png) 14.5 14.5, auto";
const CURSOR_CLICK_URL:string = "url(../games/games-cursor-click.png) 10.5 10.5, none";

interface GameState {
    gameName: string;
    isCursorPressed: boolean;
    cursorState: object;
}

export interface GameProps {
    gameName: string;
}

export default class Game<P, S extends GameState> extends React.Component<P, S> {
    private static Instance: Game<any, any>;

    constructor(props: any) {
        super(props);

        this.state = {
            gameName: window.location.href.slice(window.location.href.lastIndexOf("/") + 1),
            cursorState: {cursor: CURSOR_URL},
            isCursorPressed: false,
        } as S

        Game.Instance = this;
    }

    componentDidMount(): void {
        eventPageUpdated.addListener(PAGE_UPDATED_EVENT, this.PageUpdated);

        document.addEventListener('mousedown', () => this.OnClick(true));
        document.addEventListener('mouseup', () => this.OnClick(false));
    }

    componentWillUnmount(): void {
        eventPageUpdated.removeListener(PAGE_UPDATED_EVENT, this.PageUpdated);
        
        document.removeEventListener("mousedown", () => this.OnClick(true));
        document.removeEventListener('mouseup', () => this.OnClick(false));
    }

    private PageUpdated = () => {
        this.setState({
            ...this.state,
            gameName: window.location.href.slice(window.location.href.lastIndexOf("/") + 1),
        })
    }

    public static GetGameName(): string {
        return this.Instance.state.gameName;
    }

    private OnClick(press: boolean) {
        this.setState({
            ...this.state,
            isCursorPressed: press,
            cursorState: {cursor: press ? CURSOR_CLICK_URL : CURSOR_URL}
        })
    }

    render() {
        return (
            <div className="game-page" style={this.state.cursorState}>
                <div className="fixed-background" style={GetBackgroundImage()}></div>
                <div className="content">
                    <GameTop gameName={this.state.gameName} />
                    <GameMain gameName={this.state.gameName} />
                    {
                        GetCurrentGameProperties().nGameImages > 1 &&
                        <GameImages gameName={this.state.gameName} />
                    }
                    {/* <GameCode gameName={this.state.gameName} /> */}
                </div>
                <Contacts />
            </div>
        )
    }
}